.nav-wrapper {
  position: fixed;
  width: 100%;
  box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.1);
  position: fixed;
  z-index: 100;
  background-color: #ededed;
}

.nav-container {
  max-width: var(--max-width);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  height: 7vh;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.site-title {
  font-weight: bold;
  text-decoration: none;
  font-variation-settings: "wght" 650;
}

.nav-container ul {
  display: flex;
  gap: 1rem;
  list-style: none;
  margin: 0;
  padding: 0;
  font-variation-settings: "wght" 550;
}

.nav-container a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.5rem;
}

.nav-container ul li a {
  color: #000;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.nav-container ul li a:hover {
  color: #000000;
  background-color: #e1e1e1;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  text-decoration: none;
}

.nav-container ul li.active a {
  font-weight: bold;
  color: var(--link-color);
  border-radius: 5px;
}
